
body {
  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif; 
}
.main-container{
  background-color: #efefef;
  margin: 0px;
  padding: 0px;
}
.valueContainer{
  height: 826px;
  background-color: #000000;
  padding: 5px;
}
.gridContainer{
  height: 826px;
  background-color: #ffffff;
  padding: 5px;
  overflow-y:auto;
}
.valueBoxContainer{
  margin-top: 30%;
  margin-left: 25%;
}
.valueContainerInner{
  padding: 3%;
  margin-top: 5%;
  height: 150px;
  width: 335px;
  background-color: #efefef;
  display: grid;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
}
.textbox{
  border-radius: 5px;
  border: solid 1px #000;
  padding: 3px;
}
.submitBtn{
  background: #000;
  border: 1pxx #000 solid;
  border-color: #000;
  width: 60%;
  border-radius: 25px;
  font-weight: 100;
  font-size: 17px;
}
.submitBtn:hover{
  background: #000;
  border: 1px #000 solid;
  color: rgb(93, 142, 206);
  font-weight: 100;
  font-size: 17px;
  
}
.submitBtn:active{
  background-color: #000;
  border: 1px #000 solid;
  color: rgb(93, 142, 206);
  font-weight: 100;
  font-size: 17px;
}

.textbox-container{
  
}
.submitBtn-container{
text-align: center;

}
.ttt_font_valueContainer{
  font-size: 1.5em;
  font-weight: 100;
  font-style: italic;
  color: #ffffff;
  margin-left: -2%;
}
.ttt_font_logo{
  font-size: 4em;
  font-weight: 100;
  color: #ffffff;
  margin-left: 3%;
}
.ttt_font_c{
  font-size: 1.25em;
  font-weight: 100;
  color: #ffffff;
  margin-left: 2px;

}
.ttt_font_gridContainer{
  margin-top: 44%;
  margin-left: 35%;
}
.ttt_font_grid{
  font-size: 1.5em;
  font-weight: 100;
  font-style: italic;
  color: #000;
}
.hide-true{
  display: none;
}
